@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font: 100%/1.5 "Spartan", sans-serif;
  background-color: hsl(180, 52%, 96%);
}

button {
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
}

.filterbox {
  display: flex;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background-color: #fff;
  /* -3rem: touch main top edge */
  -webkit-transform: translateY(calc(-3rem - 40%));
          transform: translateY(calc(-3rem - 40%));
  box-shadow: 2px 10px 10px 0px hsla(180, 29%, 50%, 0.5);
}

.filterbox__filters-wrapper {
  flex: 1 1 80%;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
}

.filterbox__filter {
  display: flex;
  align-items: center;
  border-radius: 0.5rem;
  background-color: hsl(180, 31%, 95%);
  overflow: hidden;
}

.filterbox__filter p {
  padding: 0.5rem;
  font-weight: 700;
  color: hsl(180, 29%, 50%);
}

.filterbox__filter button {
  height: 100%;
  padding: 1.2rem;
  border: none;
  background-color: hsl(180, 29%, 50%);
  background-image: url(/static/media/icon-remove.ae67c678.svg);
  background-repeat: no-repeat;
  background-size: 60% 60%;
  background-position: center center;
  transition: background-color 300ms ease-in-out;
}

.filterbox__filter button:hover {
  background-color: hsl(180, 14%, 20%);
}

.filterbox__clear {
  flex: 1 1 20%;
  max-width: 5rem;
}

.filterbox__clear button {
  width: 100%;
  height: 100%;
  font-weight: 700;
  color: hsl(180, 14%, 20%);
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: color 300ms ease-in-out;
}

.filterbox__clear button:hover {
  color: hsl(180, 29%, 50%);
}

@media screen and (min-width: 764px) {
  .filterbox {
    /* -1.5rem: touch main top edge */
    -webkit-transform: translateY(calc(-1.5rem - 50%));
            transform: translateY(calc(-1.5rem - 50%));
    box-shadow: 2px 10px 10px 0px hsla(180, 29%, 50%, 0.5);
  }
}

.listing {
  position: relative;
  padding: 2rem;
  border-radius: 0.5rem;
  background-color: #fff;
  border-left: 8px solid hsl(180, 29%, 50%);
  box-shadow: 2px 10px 10px 0px hsla(180, 29%, 50%, 0.5);
}

.listing__img {
  position: absolute;
  top: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-size: cover;
  overflow: hidden;
}

.listing__img img {
  width: 100%;
  height: 100%;
}

.listing__info-wrapper {
  width: 100%;
}

.listing__info {
  display: flex;
  flex-direction: column;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  padding: 1rem 0;
}

.listing__title {
  order: 2;
  font-size: 1.1rem;
  font-weight: 700;
  color: hsl(180, 14%, 20%);
  transition: color 300ms ease-in-out;
}

.listing__title:hover {
  color: hsl(180, 29%, 50%);
}

.listing__info-company {
  order: 1;
  font-weight: 700;
}

.listing__info-company > span:not(:last-child) {
  margin-right: 1rem;
}

.listing__info-company > span:not(:first-child) {
  padding: 0.5rem 0.6rem;
  color: #fff;
  text-transform: uppercase;
  border-radius: 9999px;
}

.listing__info-company > .listing__comp-name {
  color: hsl(180, 29%, 50%);
}

.listing__info-company > .listing__new-tag {
  background-color: hsl(180, 29%, 50%);
}

.listing__info-company > .listing__featured-tag {
  background-color: hsl(180, 14%, 20%);
}

.listing__info-others {
  order: 3;
  font-weight: 500;
  color: hsl(180, 8%, 52%);
}

.listing__tags {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 1rem 0;
  border-top: 2px solid hsla(180, 8%, 52%, 0.4);
}

.listing__tags button {
  padding: 0.5rem 0.5rem;
  font-weight: 700;
  color: hsl(180, 29%, 50%);
  border-radius: 0.5rem;
  border: none;
  background-color: hsl(180, 31%, 95%);
  transition: all 300ms ease-in-out;
}

.listing__tags button:hover {
  color: hsl(180, 31%, 95%);
  background-color: hsl(180, 29%, 50%);
}

@media screen and (min-width: 764px) {
  .listing {
    display: flex;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
  }

  .listing__img {
    flex: 0 0 6rem;
    position: static;
    -webkit-transform: none;
            transform: none;
    width: 6rem;
    height: 6rem;
  }

  .listing__info-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-gap: 2rem;
    gap: 2rem;
  }

  .listing__tags {
    border: none;
    justify-content: flex-end;
  }
}

.listings {
  display: flex;
  flex-direction: column;
  grid-gap: 4rem;
  gap: 4rem;
}

@media screen and (min-width: 764px) {
  .listings {
    grid-gap: 2rem;
    gap: 2rem;
  }
}

.App-header {
  height: 10rem;
  background-color: hsl(180, 29%, 50%);
  background-image: url(/static/media/bg-header-mobile.6b5ff5a0.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.App-header h1 {
  font-size: 0;
}

.App-main {
  max-width: 1024px;
  padding: 3rem 2rem;
  margin: 0 auto;
}

@media screen and (min-width: 764px) {
  .App-main {
    padding: 1.5rem 2rem 4rem;
  }

  .App-header {
    background-color: hsl(180, 29%, 50%);
    background-image: url(/static/media/bg-header-desktop.6e2f7196.svg);
  }
}

